<template>

    <v-card-text>
        <v-text-field label="Wei" ref="weiInput" v-model="weiValue" :rules="weiRules" @focus="weiChange = true; weiValue = ''" @blur="weiChange = false" required>
            <v-tooltip slot="prepend" color="info" bottom>
                <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on" small @click="copyValue(weiValue)">mdi-content-copy</v-icon>
                </template>
                <span>Copy to clipboard</span>
            </v-tooltip>
        </v-text-field>

        <v-text-field label="GWei" ref="gweiInput" v-model="gweiValue" :rules="gweiRules" @focus="gweiChange = true; gweiValue = ''" @blur="gweiChange = false" required><v-tooltip slot="prepend" color="info" bottom>
                <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on" small @click="copyValue(gweiValue)">mdi-content-copy</v-icon>
                </template>
                <span>Copy to clipboard</span>
            </v-tooltip>
        </v-text-field>

        <v-text-field label="Ether" ref="ethInput" v-model="ethValue" :rules="ethRules" @focus="ethChange = true; ethValue = ''" @blur="ethChange = false" required>
            <v-tooltip slot="prepend" color="info" bottom>
                <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on" small @click="copyValue(ethValue)">mdi-content-copy</v-icon>
                </template>
                <span>Copy to clipboard</span>
            </v-tooltip>
        </v-text-field>
    </v-card-text>

</template>

<script>

    import { mapState } from 'vuex';
    import BN from 'bn.js';

    export default {
        name: 'Converter',

        data() {
            return {
                weiValue: '',
                weiRules: [ v => /^[0-9]*$/.test(v) || 'Must be an integer number' ],
                weiChange: false,

                gweiValue: '',
                gweiRules: [ v => /^[0-9]*$/.test(v) || 'Must be an integer number' ],
                gweiChange: false,

                ethValue: '',
                ethRules: [ v => /^[0-9]*$/.test(v) || 'Must be an integer number' ],
                ethChange: false
            }
        },

        computed: {
            ...mapState({
                'w3': state => state.chain.w3
            })
        },

        watch: {
            weiValue(val) {
                this.$refs.weiInput.validate();

                if (this.weiChange && this.$refs.weiInput.valid) {
                    const v = new BN(val, 10);

                    this.gweiValue = this.divBN(v, 9);
                    this.$refs.gweiInput.resetValidation();
                    this.ethValue = this.divBN(v, 18);
                    this.$refs.ethInput.resetValidation();
                }
            },

            gweiValue(val) {
                this.$refs.gweiInput.validate();

                if (this.gweiChange && this.$refs.gweiInput.valid) {
                    const v = new BN(val, 10);

                    this.weiValue = this.w3.utils.toWei(v, 'gwei').toString();

                    this.ethValue = this.divBN(v, 9);
                    this.$refs.ethInput.resetValidation();
                }
            },

            ethValue(val) {
                this.$refs.ethInput.validate();

                if (this.ethChange && this.$refs.ethInput.valid) {
                    const v = new BN(val, 10);

                    this.weiValue = this.w3.utils.toWei(v, 'ether').toString();
                    this.gweiValue = this.w3.utils.fromWei(this.weiValue, 'gwei').toString();
                }
            },
        },

        methods: {
            copyValue(val) {
                if (navigator.clipboard) {
                    navigator.clipboard.writeText(val);
                }
            },

            divBN(x, n) { 
                const base = new BN(10).pow(new BN(n));
                const dm = x.divmod(base);
                return dm.div + "." + dm.mod.toString(10, n)
            }
        }

    }

</script>
